var shortTimeFT = require("stft");

export function computeSpectralPower(xdata: Number[], ydatas: Int16Array[]) {
    const windowSize = 64;
    const hopSize = 4;
    // TODO consider using typed arrays here.
    let mags: number[][][] = []
    for (let y = 0; y < ydatas.length; y++) {
        let mag = []
        function onFreq(re, im) {
            let m = []
            for (let i = 0; i < re.length / 2 + 1; i += 2) {
                let intensity = (i) => re[i] * re[i] + im[i] * im[i];
                let avgPower = 10 * Math.log10((intensity(i) + intensity(i + 1)) / 2);
                // TODO deal with zero power better.
                if (Math.abs(avgPower) !== Infinity) {
                    m.push(avgPower);
                }
            }
            if (m.length > 0) {
                mag.push(m);
            }
        }
        let stft = shortTimeFT.stft(windowSize, onFreq, { hop_size: hopSize });
        let ydata = ydatas[y];
        for (let i = 0; i < ydata.length; i += windowSize) {
            let temparray = new Float32Array(windowSize);
            for (let j = 0; i + j < ydata.length; j++) {
                temparray[j] = ydata[i + j];
            }
            stft(temparray);
        }
        mags.push(mag);
    }
    return mags;
}