import { Data } from "./types";
import { ECOGChart } from './ecog-chart';
import { LitElement, html, property } from '@polymer/lit-element';
import { until } from '../node_modules/lit-html/directives/until';
import { EEGFileMetadata } from "../../backend/src/types";
import '@polymer/paper-checkbox';

class EcogViewer extends LitElement {

  datas: Data[] = []
  @property()
  epoch: string;
  @property()
  patient: string;
  @property({ type: Number })
  gain: number;
  @property({ type: Number })
  datasIndex = 0;

  chart: ECOGChart;
  lastLoadedEpoch: string;

  constructor() {
    super()
  }


  render() {
    super.render();

    // Resolve immediately if we've already loaded.
    console.log('render viewer');
    let promise = Promise.resolve();
    if (this.patient !== undefined
      && this.epoch !== undefined
      && this.lastLoadedEpoch != this.epoch) {
      console.log('loading data');
      let that = this;
      let epochEncoded = encodeURIComponent(this.epoch);
      let datas: Data[] = [];
      let i = 0;
      // metadata.map(m => {
      //   const view = new DataView(arr);
      //   const length = view.getUint32(i);
      //   const tarr = new Int16Array(arr.slice(i + 4, i + 4 + length));
      //   let channels = []
      //   for (let j = 0; j < 4; j++) {
      //     channels.push(new Int16Array(length / 2 / 4));
      //   }
      //   for (let j = 0; j < tarr.length; j++) {
      //     channels[j % 4][Math.floor(j / 4)] = tarr[j] - 512;
      //   }
      //   datas.push({
      //     channels: channels,
      //     metadata: m,
      //   });
      //   i += length + 4;
      // });
      // console.log('Loaded', datas.length, 'eegs');
      // that.datas = datas;
      // that.datasIndex = 0;
      // that.chart.data = that.datas[this.datasIndex];
      // that.chart.patient = that.patient;
      // that.lastLoadedEpoch = that.epoch;
    }
    // if (this.lastLoadedEpoch !== undefined) {
    //   this.chart.data = this.datas[this.datasIndex];
    // }
    return html`
  <link rel="stylesheet" href="https://fonts.googleapis.com/icon?family=Material+Icons">
  <link rel="stylesheet" href="https://unpkg.com/material-components-web@latest/dist/material-components-web.min.css">
  <link rel="stylesheet" href="style.css">
  <div id="main" role="main">
    <div id="content">
      <div id="ecog-chart"></div>
      <aside id="sidebar">
        <style>
        td a { 
          display: block; 
        }
        tr { 
          font-size: 20pt;
        }
        .link-td {
          padding: 0px;
        }
        a {
          padding: 5px;
        }
        tr:hover { 
          background-color: lightgrey;
        }
        table {
          width: 300px;
        }
        </style>
        <table class="highlight">
        <thead>
          <tr>
            <th>Index</th>
            <th>Time</th>
            <th>Annotation</th>
          </tr>
          </thead>
      ${until(promise.then(_ => {
      return this.datas.map((d, i) => {
        return { index: i, annotations: d.metadata.annotations }
      }).filter(d => d.annotations.length > 0)
        .map(ann => {
          return ann.annotations.map(a => {
            return html`
            <tr @click=${_ => {
                this.datasIndex = ann.index;
                this.chart.scrollTo(a.sample_nmbr / 250);
              }}>
              <td class="link-td">${ann.index + 1}</td>
              <td class="link-td">${a.sample_nmbr / 250}</td>
              <td class="link-td">${a.name}</td>
            </tr>`
          })
        }).reduce((acc, val) => acc.concat(val), []);
    }), '')}
        </table>
      </aside>
    </div>
  </div>
  <footer>
      ${until(promise.then(_ => {
      return html`<span class="footer-element">
    </span>
    <span class="footer-element">
      ${until(promise.then(_ => html`Gain: ${this.chart.gain}x`), '')}
    </span>
    <div class="footer-element">
      <button @click="${this.handlePrev}" class="mdc-fab" aria-label="Previous">
        <span class="mdc-fab__icon material-icons">arrow_back</span>
      </button>
      <button @click="${this.handleNext}" class="mdc-fab" aria-label="Next">
        <span class="mdc-fab__icon material-icons">arrow_forward</span>
      </button>
      <button @click="${this._handleSkip}" class="mdc-fab" aria-label="Skip" title="Skip to next unreviewed recording.">
        <span class="mdc-fab__icon material-icons">skip_next</span>
      </button>
      <button @click="${this._showHideSpectrogram}" class="mdc-fab" aria-label="Show/hide spectrogram" title="Skip to next unreviewed recording.">
        <span class="mdc-fab__icon material-icons">waves</span>
      </button>
    </div>
    <span class="footer-element">
    </span>
    <span class="footer-element">
        ${this.datasIndex + 1} / ${this.datas.length}
    </span>
    </div>
      `
    }), html`
    <div role="progressbar" class="mdc-linear-progress--indeterminate">
      <div class="mdc-linear-progress__buffering-dots"></div>
      <div class="mdc-linear-progress__buffer"></div>
      <div class="mdc-linear-progress__bar mdc-linear-progress__primary-bar">
        <span class="mdc-linear-progress__bar-inner"></span>
      </div>
      <div class="mdc-linear-progress__bar mdc-linear-progress__secondary-bar">
        <span class="mdc-linear-progress__bar-inner"></span>
      </div>
    </div>
  </footer>
    `)}
    `
  }

  updated(_changedProperties) {
    super.updated(_changedProperties);
    let div = this.shadowRoot.querySelector("#ecog-chart");
    if (this.chart === undefined && div !== null) {
      console.log("new chart");
      this.chart = new ECOGChart(div as HTMLDivElement);
    }
  }

  handlePrevNext(next: boolean) {
    if (next) {
      if (this.datasIndex < this.datas.length - 1) {
        this.datasIndex++;
      }
    } else {
      if (this.datasIndex > 0) {
        this.datasIndex--;
      }
    }
  }

  handlePrev() {
    this.handlePrevNext(false);
  }

  handleNext() {
    this.handlePrevNext(true);
  }

  _handleSkip() {
    while (this.datasIndex < this.datas.length
      && this.datas[this.datasIndex].metadata.reviewed) {
      this.datasIndex++;
    }
  }

  markReviewed() {
    this.datas[this.datasIndex].metadata.reviewed = true;
    this.chart.uploadMetadata();
    this.render();
  }

  _handleCheck(e) {
    let m = this.datas[this.datasIndex].metadata;
    m.reviewed = !m.reviewed;
    this.chart.uploadMetadata();
  }

  _showHideSpectrogram() {
    this.chart.showSpectrogram = !this.chart.showSpectrogram;
    this.chart.render();
  }
}

customElements.define('ecog-viewer', EcogViewer);
