import { LitElement, html, property } from '@polymer/lit-element';
import { Patient } from '../../backend/src/types'

class PatientList extends LitElement {
  @property({ type: Boolean })
  visible = true;

  @property()
  patients: Patient[];

  constructor() {
    super();
    this.patients = [];
    let that = this;
    fetch('/api/patients')
      .then(response => response.json())
      .then(patients => that.patients = patients);
  }

  render() {
    return html`
      <link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/materialize/1.0.0/css/materialize.min.css">
      <h2>Patients</h2>
      <style>
      :host {
        overflow: scroll !important;
      }
      td a { 
        display: block; 
      }
      tr { 
        font-size: 20pt;
      }
      .link-td {
        padding: 0px;
      }
      a {
        padding: 5px;
      }
      tr:hover { 
        background-color: lightgrey;
      }
      </style>
      <table class="highlight">
      <thead>
        <tr>
          <th>RNS ID</th>
          <th>Initials</th>
        </tr>
        </thead>
        ${this.patients.map((patient: Patient) => {
        return html`
          <tr>
            <td class="link-td"><a href="#/epochs/${patient.rns_deid_id}">${patient.rns_deid_id}</a></td>
            <td class="link-td"><a href="#/epochs/${patient.rns_deid_id}">${patient.initials_short}</a></td>
          </tr>`
      })}
      </table>
      `;
  }
}

customElements.define('patient-list', PatientList);