import { LitElement, html, property } from '@polymer/lit-element';
import { until } from '../node_modules/lit-html/directives/until';
import { Epoch } from '../../backend/src/types'
import { active } from 'd3';

class EpochList extends LitElement {
  @property()
  route;

  @property()
  patient: string;

  @property()
  active: boolean;

  constructor() {
    super();
  }

  render() {
    return html`
      <link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/materialize/1.0.0/css/materialize.min.css">
      <h2>Epochs for ${this.patient}</h1>
      <style>
      :host {
        overflow: scroll !important;
      }
      td a { 
        display: block; 
      }
      tr { 
        font-size: 20pt;
      }
      .link-td {
        padding: 0px;
      }
      a {
        padding: 5px;
      }
      tr:hover { 
        background-color: lightgrey;
      }
      </style>
      ${until(fetch(`/api/epochs?rns_deid_id=${this.patient}`)
        .then(response => response.json())
        .then(epochs => {
          return html`<table class="highlight">
      <thead>
        <tr>
          <th>Date</th>
          <th>Time</th>
          <th>Recordings</th>
          <th>Marking Progress</th>
        </tr>
        </thead>
        ${epochs.map((e: Epoch) => {
              const date = new Date(e.programming_dts).toLocaleDateString();
              const time = new Date(e.programming_dts).toLocaleTimeString();
              const href = `#/ecog/${this.patient}/${encodeURIComponent(e.programming_dts as string)}`;
              return html`
          <tr>
            <td class="link-td">
              <a href="${href}">
              ${date}
            </a></td>
            <td class="link-td">
              <a href="${href}">
              ${time}
            </a></td>
            <td class="link-td">
              <a href="${href}">
              ${e.recordings_count}
            </a></td>
            <td class="link-td">
              <a href="${href}">
              ${e.recordings_count === 0 ? '-' : Math.round(e.reviewed_count / e.recordings_count * 1000) / 10 + '%'}
            </a></td>
          </tr>`
            })}
      </table>`}), html`Loading...`)}
      `;
  }
}

customElements.define('epoch-list', EpochList);